import React, { useEffect, useState } from 'react';

const SuccessMensaje  = ({getShowSuccessMensaje, msj}) =>{

    useEffect(()=>{
        setTimeout(change, 4000);
    },[]); 
    const change=()=>{
        getShowSuccessMensaje(false);
    }

    return (
        <div className="alert alert-success">
            <span>{ msj }</span><a className="cerrarMsj" onClick={()=>getShowSuccessMensaje(false)}>X</a>
        </div>
    );
}
export default SuccessMensaje;