import React from 'react';
import './Proyectos.css';

const ProyectosElementos = ({ item, img }) =>{

    console.log(img)
    const elem = item.lenguajes;
    return (    
        <div className="card proyecto-card">
        <div className="proyecto-img"> 
        <img src={img} alt={`Proyecto${item.id}`}/> 
        </div>
            <div className="proyecto-descrip">
                <h2>{item.titulo} <br/> <span>{item.descripcion}{item.extra}</span></h2>
                    <label>Lenguajes/Librerias usadas:</label>
                    <div className="lista-proyectos">
                        {elem.map((len, index)=>(
                            <>
                           <div className="lenguajes"><span className={len} >{len}</span></div> 
                           <div><span style={{fontWeight:"bold"}}>{ item.separador[index] }</span></div> 
                           </>
                        ))}
                    </div> 
                    <a className="btn" href={item.link}>Visitar</a>
                </div>
            </div>
    );
}

export default ProyectosElementos;