import logo from './logo.svg';
import './App.css';
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect, useState } from 'react';
import NavBar from './componentes/NavBar';
import Header from './componentes/Header/Header';
import SobreMi from './componentes/SobreMi/SobreMi';
import Proyectos from './componentes/Proyecto/Proyectos';
import Skill from './componentes/Skill/Skill';
import Footer from './componentes/Footer/FooterPage';
import Email from './componentes/Email/Email';
import SuccessMensaje from './componentes/SuccessMensaje';
import ErrorMensaje from './componentes/ErrorMensaje';

function App() {
  const [mostrarEmail, setMostrarEmail] = useState(false);
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [showErrorMessage, setShowErrorMessage] = useState(true);
  const [msj, setMsj] = useState("");

  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  useEffect(() => {
    if (showErrorMessage) {
      setTimeout(setShowErrorMessage(false), 2000);
    }
    else if (showSuccessMessage) {
      setTimeout(setShowSuccessMessage(false), 2000);
    }
  }, []);

  return (
    <div className="App">
      <NavBar getMostrarEmail = {(e)=>setMostrarEmail(e)} mostrarEmail={mostrarEmail}/>

      <div className="grid">
      
        <div id="inicio" style={{ height: "1rem" }}></div>
          <section data-aos="flip-up">
            <Header />
          </section>
      
        <div id="sobreMi" style={{ height: "1%" }}></div>
          <section className="sobreMi" data-aos="slide-up">
            <SobreMi />
          </section>
      
        <div id="proyectos" style={{ height: "2rem" }}></div>
          <section className="proyectos" data-aos="slide-up">
            <h2 class="titulo" id="proyecto">Algunos proyectos realizados</h2>
            <Proyectos />
          </section>
      
        <div id="skill" style={{ height: "2rem" }}></div>
          <section className="conocimientos" data-aos="slide-up">
            <h2 class="titulo" id="conocimiento">Conocimientos</h2>
            <Skill />
          </section>
          <div id="contacto" style={{ height: "2rem" }}></div>
          {
            showSuccessMessage ? 
            <SuccessMensaje getShowSuccessMensaje={(b)=> setShowSuccessMessage(b)} msj={msj}/>
            :
            <></>
          }
          {
            showErrorMessage ? 
            <ErrorMensaje getShowErrorMensaje={(b)=> setShowErrorMessage(b)} msj={msj}/>
            :
            <></>
          }
        {
        mostrarEmail ? 
        <>
        <div id="mail" style={{ height: "1rem" }}></div>
        <section data-aos="slide-up">
            <Email mostrarEmail={mostrarEmail} getMostrarEmail = {(e)=>setMostrarEmail(e)} getShowSucessMessage= {(b)=>setShowSuccessMessage(b)}  getShowErrorMessage= {(v)=>setShowErrorMessage(v)} getMsj={(m)=>setMsj(m)}/>
          </section>
          </>
        :
        <></>
        }
      </div>
      
    </div >
  );
}

export default App;
