import React, { useState, useEffect } from 'react';
import './Proyectos.css';
import p1 from "../IMG/p1.jpg";
import p2 from "../IMG/p2.jpg";
import p3 from "../IMG/dadJK.png";
import p4 from "../IMG/p4.jpg";
import p5 from "../IMG/p5.png";
import p6 from "../IMG/p6.png";
import p7 from "../IMG/p7.png";
import ProyectosElementos from './ProyectosElemento';

import {data} from './ProyectoLista.json'

const Proyectos = () => {
   const arregloImg = [p1, p2, p3, p4, p5, p6, p7]
    return (
        <div className="container-fluid contenedor-proyectos text-body">
            {data.map((item, index) => (
              <ProyectosElementos key={item.id} item={item} img={arregloImg[index]}/>
            ))}
        </div>
    )
}

export default Proyectos;