import React, { useRef, useState } from "react";
import emailjs from "emailjs-com";
import "./Email.css";

const Email = ({ getMostrarEmail, getShowSucessMessage, getMsj, getShowErrorMessage }) => {
  const frmContact = { userEmail: "", userName: "", message: "", replyTo: "" };
  const [contact, setContact] = useState(frmContact);
  const slider = useRef();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setContact({ ...contact, [name]: value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    console.log(contact.userEmail);
    console.log(contact.userName);
    console.log(contact.message);
    console.log(contact.replyTo);

    if (contact.userEmail === '' || contact.userName === '' || contact.message === '') {
      alert("No puedes dejar elementos en blanco")
    }
    else {
      emailjs
        .send(
          "default_service",
          "template_s273pg4",
          contact,
          "user_0ofd7UXqQrOz5FA4BbFHj"
        )
        .then(
          (response) => {
            console.log("SUCCESS!", response.status, response.text);
            setContact(frmContact);
            getShowSucessMessage(true);
            getShowErrorMessage(false);
            getMsj("Mensaje Enviado");
          },
          (err) => {
            console.log("FAILED...", err);
            getShowErrorMessage(true);
            getShowSucessMessage(false);
            getMsj("Error, no se pudo enviar el mensaje");
          }
        );

      const toggleNode = slider.current;
      toggleNode.classList.toggle('toggled');
      console.log(toggleNode.classList.value);
      setTimeout(() => { getMostrarEmail(false) }, 3500);


    }
  };

  const cancelar = (e) => {
    e.preventDefault();
    const toggleNode = slider.current;
    toggleNode.classList.toggle('toggled');
    console.log(toggleNode.classList.value);
    setTimeout(getMostrarEmail(false), 3500);

  }

  return (
    <>
      <div class="background" ref={slider}>
        <div class="container-fluid">
          <div class="screen">
            <div class="screen-header">
              <div class="screen-header-right">
                <div class="screen-header-button cerrar"></div>
                <div class="screen-header-button maximize"></div>
                <div class="screen-header-button minimize"></div>
              </div>
            </div>
            <div class="screen-body">
              <div class="screen-body-item left">
                <div class="app-title">
                  <span>Contáctame</span>
                </div>
              </div>
              <div class="screen-body-item">
                <div class="app-form">
                  <div class="app-form-group">
                    <input
                      class="app-form-control"
                      placeholder="Tu Nombre"
                      value={contact.userName}
                      type="text"
                      name="userName"
                      onChange={handleChange}
                      required
                    />
                  </div>
                  <div class="app-form-group">
                    <input
                      class="app-form-control"
                      placeholder="Tu Email"
                      required
                      type="text"
                      value={contact.userEmail}
                      name="userEmail"
                      onChange={handleChange}
                    />
                  </div>
                  <div class="app-form-group">
                    <input
                      class="app-form-control"
                      placeholder="Otra forma de contacto (opcional)"
                      required
                      type="text"
                      value={contact.replyTo}
                      name="replyTo"
                      onChange={handleChange}
                    />
                  </div>
                  <div class="app-form-group message">
                    <input
                      class="app-form-control"
                      required
                      name="message"
                      onChange={handleChange}
                      placeholder="Escribe tu mensaje"
                      value={contact.message}
                    />
                  </div>
                  <div class="app-form-group buttons">
                    <a href="#contacto">
                      <button class="app-form-button" onClick={handleSubmit}>
                        Enviar
                    </button>
                    </a>
                    <a href="#skill">
                      <button
                        class="app-form-button"
                        onClick={(e) => cancelar(e)}
                      >
                        Cancelar
                    </button>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Email;

