import React from 'react';
import imagenPersonal from '../componentes/IMG/4Ju1tR9uqXY.jpg'
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';
import ExtensionOutlinedIcon from '@material-ui/icons/ExtensionOutlined';
import CommentOutlinedIcon from '@material-ui/icons/CommentOutlined';

const NavBar = ({ getMostrarEmail, mostrarEmail }) => {
  return (
    <nav
      id="barra"
      className="navbar navbar-expand-md bg-dark navbar-dark fixed-top"
      style={{ width: "100%" }}
    >
      <a className="navbar-brand" href="#inicio">
        <img
          src={imagenPersonal}
          alt="img-personal"
          style={{ width: "40px" }}
        />
      </a>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#collapsibleNavbar"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse justify-content-end" id="collapsibleNavbar">
        <ul className="navbar-nav">
          <li className="nav-item">

            <div className="social-buttons">
              <a className="social-buttons__button social-button social-button--about p-0" href="#sobreMi" title="Sobre mi" aria-label="Sobre mi">
                <span className="social-button__inner">
                  <PermIdentityIcon className="  acount" />
                </span>
              </a>
            </div>

          </li>

          <li className="nav-item">

            <div className="social-buttons">
              <a className="social-buttons__button social-button social-button--proyecto p-0" href="#proyectos" title="Proyectos" aria-label="Proyectos">
                <span className="social-button__inner">
                  <WorkOutlineIcon className="  acount" />
                </span>
              </a>
            </div>
          </li>
          <li className="nav-item">
            <div className="social-buttons">
              <a className="social-buttons__button social-button social-button--skill p-0" href="#skill" title="Habilidades" aria-label="Habilidades">
                <span className="social-button__inner">
                  <ExtensionOutlinedIcon className="  acount" />
                </span>
              </a>
            </div>
          </li>
          <li className="nav-item">
            <div className="social-buttons" onClick={()=>getMostrarEmail(!mostrarEmail)}>
              <a className="social-buttons__button social-button social-button--contact p-0" href="#contacto" title="Contactos" aria-label="Contactos">
                <span className="social-button__inner">
                  <CommentOutlinedIcon className="acount" />
                </span>
              </a>
            </div>
          </li>
        </ul>
      </div>
    </nav>
  );
}

export default NavBar;