import React from 'react';
import '../Skill/Skill.css';
import html from '../IMG/html.png';
import css from '../IMG/css_blog.png';
import ts from '../IMG/positronx-banner-1152-1.jpg';
import angular from '../IMG/angular2.png';
import js from '../IMG/js.png';
import react from '../IMG/react.jpg';
import { skill } from './SkillLista.json';

const Skill = () => {
  const arregloImg = [html, css, ts, angular, js, react];

  return (
    <div class=".container-fluid contenedor-skill">

      {skill.map((item, index) => (
        <div class="flip-card">
          <div class="flip-card-inner">
            <div class="flip-card-front">
             
                <img src={arregloImg[index]} alt={item.name} style={{ width: "100%", height:"100%"}} />
              
            </div>
            <div class="flip-card-back">
            <a class="link" href={item.link} target="_blank"><h3 className="mt-2 h3FlipCard" data-content={item.name}>{item.name}</h3></a>
              <p style={{fontSize:"14px"}}>
                {item.desc}
              </p>
            </div>
          </div>
        </div>
      ))}

    </div>
  );
}

export default Skill;