import React, { useRef, useEffect} from 'react';
import './Sobremi.css';

const SobreMi = () =>{
  const escribir = useRef();
  let i = 0;
  const txt =
  "Bienvenid@: Soy Estefanía Atenas Núñez, estudiante de último año de Ingeniería de ejecución en informática de la universidad de las Américas, me considero responsable, proactiva, eficiente, autodidacta y en constante aprendizaje de diversas tecnologias. Me especializo en desarrollo web, específicamente en Frontend; poseo experiencia utilizando Frameworks como Angular y bibliotecas como ReactJS. He desarrollado aplicaciones mobile para pymes utilizando Java for Android.";
  const speed = 50;

  useEffect(()=>{
    setTimeout(setTimeout(typeWriter, speed), 2000);
  }, []);
  
const typeWriter = () => {
  const escribirNode = escribir.current;
  if (i < txt.length) {
    escribirNode.innerHTML += txt.charAt(i);

    if (txt.charAt(i) == ":" || (txt.charAt(i)=="." && txt.charAt(i+2)!="H")) {
      escribirNode.innerHTML += "<br><br>";
    }
    i++;
    setTimeout(typeWriter, speed);
  }
    }
    
  return(
        <div className="container-fluid">
          <h2 className="titulo">Sobre mí</h2>
          <div className="texto">
            <p className="direccion">
              <span className="dir">C: portafolio/eatenas</span>
            </p>
            <br />
            <p className="descripcion">
              $
              <span id="tagline" ref={escribir}> </span>
            </p>
          </div>
        </div>
    );
}

export default SobreMi;