import React, { useEffect, useState } from 'react';

const ErrorMensaje  = ({getShowErrorMensaje, msj}) =>{

    useEffect(()=>{
        setTimeout(change, 2000);
    },[]);

    const change=()=>{
        getShowErrorMensaje(false);
    }


    return (
        <div className="alert alert-danger">
        <a className="cerrarMsj" onClick={()=>getShowErrorMensaje(false)}>X</a><span>{ msj }</span>
        </div>
    );
}
export default ErrorMensaje;